
import { defineComponent } from "vue";
import { User } from "@/store/state";
import Dropdown from "./Dropdown.vue";

export default defineComponent({
  name: "User",
  components: {
    Dropdown,
  },
  data() {
    return {
      showDropdown: false,
      dropdown: [
        {
          name: "Osobni podaci",
          icon: "fingerprint",
          link: "/osobni-podaci",
        },
        {
          name: "Odjava",
          icon: "exit_to_app",
          link: "/",
        },
      ],
    };
  },
  computed: {
    user(): User | undefined {
      return this.$store.getters.user;
    },
  },
});
