
import { defineComponent, PropType } from "vue";

export default defineComponent({
  name: "NavbarList",
  props: {
    list: {
      type: Array as PropType<Array<{ name: string; icon: string }>>,
      required: true,
    },
    rootLink: {
      type: String,
      required: true,
    },
    order: {
      type: Number,
      required: true,
    },
  },
  computed: {
    navCollapsed(): boolean {
      return (this.$parent as any).$data.navCollapsed;
    }
  }
});
